import React from 'react';
import axios from 'axios';
import Button from "@material-ui/core/Button";
import DataTable from '../../../UI/DataTable';
import {BiAddToQueue} from 'react-icons/bi';
import './Dashboard.css';

class Campaigns extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            data: []
        };
        this.getData();
    }

    getData = async () => {
        await axios.get('/data')
            .then(res => {
                this.setState({
                    loading: false,
                    data: [{ id: 1, name: 'Sucess', date: '1991.11.11', list: 'Sucess', buttons: true }],
                });
            })
            .catch(err => {
                console.log(err);
                this.setState({
                    loading: false,
                    data: [
                        { id: 1, name: 'Error', date: '1992.11.11', list: 'Error', buttons: true },
                        { id: 2, name: 'Error', date: '1991.11.11', list: 'Error', buttons: false }
                    ]
                });
            });

    }

    render() {
        return (
            <div>
                <div className="DashboardHeadingWrapper">
                    <h2 className="DashboardHeading">Campaigns</h2>
                    <Button className="NewCampaign" ><BiAddToQueue /> CREATE NEW CAMPAIGN </Button>
                </div>
                <DataTable rows={this.state.data} loading={this.state.loading} />
            </div>
        );
    }

}

export default Campaigns;
